const Line = posed(styled.a`
z-index:999;
    position:absolute;
    content:'';
    min-height:${({ShuffleStatus})=>ShuffleStatus==='shuffleIn'?'50%':'100%'};
    width:1px;
    top: 50%;
    transform: translateY(-50%) skew(20deg);
    left: 50%;
    background-color:${({theme})=>theme.card.lines};

    @media(max-width:800px){
      opacity:${({status})=> status === 'hide' || status === 'show'?0:1}!important;
    };
    }

`)({
  // Line Animation
  show:{
    opacity:1,
    y:"-50%",
    x:({side})=>side==="left"?"25vw":"-25vw",
    skew:"0deg"
  },
  hide:{
    x:({side})=>side==="left"?"25vw":"-25vw",
    skew:"0deg",
opacity:1,
minHeight: "30%"
  },
  neutral:{
    x:"0",
y:"-50%",
skew:({ShuffleStatus})=>{
  if(ShuffleStatus==='shuffleOut'){
    return '20deg'
  }
  return '30deg'
}
  },
  shuffleOut:{
    skew:"20deg",
    minHeight: "100%"
  },shuffleIn:{
    skew:"30deg",
    minHeight: "30%"
  }
})

const Header = posed(styled.h2`
    position:absolute;
    opacity:1 !important;
`)({
  // Header Animation
  shuffleIn:{
    left:"40%"
  },
  shuffleOut:{
    y: ({index}) => "-" + index * 80 + "%",
    left:({index})=>{
       return 40+"vw"
    }
  }
})

const Paragraph = posed(styled.p`
padding-right:'0px';
`)({
  shuffleIn:{
    opacity:1
  },
  shuffleOut:{
    opacity:0
  }
})



// Component Inital State !!!!!!!
    // 2 Boxs for each row has three states left right and center 


const initalState = {
  skillsStatus: "center"
};

// Main Component Export !!!!!!!!!

export default class PortCard extends Component {
  constructor(props) {
    super(props);

    this.state = initalState;
  }

  componentDidMount() {}

  reset(){
    this.setState({
      skillsStatus:'center'
    })
  }


  toggle(side) {
    
      if(side === this.state.skillsStatus){
        side = 'center'
      }

    this.setState({
      skillsStatus: side
    });
  }

  render() {
    const { cardData,ShuffleStatus,index } = this.props;
    const { skillsStatus } = this.state;

        if(ShuffleStatus === 'shuffleOut' && skillsStatus!=="center"){
          this.reset()
        }
        
    return (
      <Container
      ShuffleStatus={ShuffleStatus}
      skillsStatus={skillsStatus}
        index={index}
      >
        <Header index={index}  >{cardData.name}</Header>
        <DisplayBox
        ShuffleStatus={ShuffleStatus}
          skillsStatus={skillsStatus}
          side={"left"}
          {...this.props}
          toggle={this.toggle.bind(this)}
          name="Details"
        />
        <DisplayBox
        ShuffleStatus={ShuffleStatus}
          skillsStatus={skillsStatus}
          side={"right"}
          {...this.props}
          toggle={this.toggle.bind(this)}
          name="Stack"
          right
        />
      </Container>
    );
  }
}

const DisplayBox = ({
  cardData,
  skillsData,
  skillsStatus,
  ShuffleStatus,
  toggle,
  side,
  name
}) => {

  // Is Side Active

  const status = ()=> {
    
    if(skillsStatus === "center"){
      return "neutral"
    }
    if(skillsStatus === side){
      return "show"
    }
    else{
      return "hide"
    }
  }

  return (
    <Box  pose={status()} >
      <SubContainer side={side} pose={status()}>
        {side === "left" ? (
          <Info cardData={cardData} />
        ) : (
          <Skills cardData={cardData.skills} skillsData={skillsData} />
        )}
        </SubContainer>
        <Line status={status()}  ShuffleStatus={ShuffleStatus} side={side} />
      <Button
      ShuffleStatus={ShuffleStatus}
        onClick={() => toggle(side)}
        status={status()}
        side={side}
        >
        <Paragraph  >{name}</Paragraph>
      </Button>
    </Box>
  );
};

const Info = ({ cardData }) => {
  
  return (
    <Container_Info  >
      <a href={cardData.gitLink}>Github</a>
      <p  style={{gridRow:"1 / 3",gridColumn:"2/3"}} >{cardData.discription}</p>
      <a href={cardData.pageLink}>Visit</a>
    </Container_Info>
  );
};

const Skills = ({ skillsData, cardData }) => {
  return (
    <Container_Skills>
      {/* <WebsitePreview img={cardData.previewImg} />{" "} */}
      {skillsData.map((el, i) => {
        let url = null;
        let val;

        cardData.map(cardskill => {
          if (cardskill === el.name) {
            url = el.img;
            val = <Img key={i} src={url} alt="" />;
          }
        });

        return val;
      })}
    </Container_Skills>
  );
};
